import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Table, Col, Button } from 'antd';
import { partyMem } from 'dump/data';
import './style.scss';
import FormSearch from './FormSearch';

const columns = [
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Giới tính',
    dataIndex: 'sex',
    key: 'sex',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'className',
  },
  {
    title: 'Chuyên ngành',
    dataIndex: 'specialized',
    key: 'specialized',
  },
  {
    title: 'Khóa học',
    dataIndex: 'courses',
    key: 'courses',
  },
  {
    title: 'Tên khoa',
    dataIndex: 'khoa',
    key: 'khoa',
  },
  {
    title: 'Tên hệ',
    dataIndex: 'sysName',
    key: 'sysName',
  },
  {
    title: 'Ngày kết nạp',
    dataIndex: 'joinDay',
    key: 'joinDay',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      partyMem.length >= 1 ? (
        <Fragment>
          <Button className="btnNoiNgoaiTru" type="primary" href={`#party-mem/form/${record.code}`}>
            Kết nạp
          </Button>
          <Button className="btnNoiNgoaiTru" type="primary">
            Khai trừ
          </Button>
        </Fragment>
      ) : null,
  },
];

class StuPartyMemPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Đảng viên là sinh viên">
        <Row>
          <Col span={24}>
            <FormSearch />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={partyMem} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StuPartyMemPage />
  </Page>
);



