import React, { Component } from 'react';
import { Row, Col, Form, Select, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { BtnSearch, BtnClear } from 'components/common/button';

const { Option } = Select;

@Form.create({ name: 'formSerchIndividual' })
class FormSerchIndividual extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={8}>
            <Form.Item label="Mã SV">{getFieldDecorator('code', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Họ tên">{getFieldDecorator('name', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Loại đối tượng">
              {getFieldDecorator('typeObject', {})(
                <Select>
                  <Option value="ok">Loại đối tượng</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSerchIndividual;



